<template>
  <div>
    <el-row
      ><el-select
        v-model="testMethod"
        placeholder="请选择"
        @change="changeMethod"
      >
        <el-option
          v-for="item in testOptions"
          :key="item"
          :label="item"
          :value="item"
        >
        </el-option>
      </el-select>
      <el-button type="primary" @click="Test()">测试</el-button>
    </el-row>
    <el-row
      ><Table
        class="result-table"
        v-show="JSON.stringify(testResults) != '{}'"
        :testResults="testResults"
      ></Table
    ></el-row>
    <el-row>
      <MyCharts
        class="result-chart"
        v-show="JSON.stringify(testResults) != '{}'"
        :dataSet="testResults"
      ></MyCharts>
    </el-row>
  </div>
</template>

<script>
import MyCharts from "./MyCharts.vue";
import Table from "./Table.vue";

export default {
  props: ["activePath"],
  components: {
    MyCharts,
    Table,
  },
  data() {
    return {
      //测试方法
      testMethod: "",
      //测试结果
      testResults: {},
    };
  },
  created() {},
  watch: {
    activePath: function (val) {
      this.testResults = {}; //清空测试结果
      this.testMethod = ""; //清空测试方法
    },
  },
  computed: {
    //测试选择
    testOptions: function () {
      let options = [];
      switch (this.activePath) {
        //三角形问题
        case "/triangle":
          options = ["BoundaryValue", "EquivalenceClass"];
          break;
        //电脑销售问题
        case "/commission":
          options = ["BoundaryValue"];
          break;
        //电信收费问题
        case "/charge":
          options = ["BoundaryValue", "EquivalenceClass", "DecisionTable"];
          break;
        //万年历问题
        case "/calendar":
          options = ["BoundaryValue", "EquivalenceClass"];
          break;
      }
      console.log(options);
      return options;
    },
  },
  methods: {
    //测试
    async Test() {
      let results = {};
      switch (this.activePath) {
        //三角形问题
        case "/triangle":
          results = await this.$http.get(
            "/question1/triangle/" + this.testMethod
          );
          break;
        //电脑销售问题
        case "/commission":
          results = await this.$http.get(
            "/question3/commission/" + this.testMethod
          );
          break;
        //电信收费问题
        case "/charge":
          results = await this.$http.get(
            "/question6/charge/" + this.testMethod
          );
          break;
        //万年历问题
        case "/calendar":
          results = await this.$http.get(
            "/question8/calendar/" + this.testMethod
          );
          break;
      }
      this.testResults = results.data;
      console.log(this.testResults);
    },
    //改变选择值
    changeMethod(newMethod) {
      this.testResults = {};
      console.log(this.testResults);
    },
  },
};
</script>

<style lang="less" scoped>
.el-select {
  margin-right: 20px;
}
.result-table {
  margin: 50px auto 30px auto
}
.result-chart {
  margin: 0 auto;
}
</style>